/*OverlayContainer*/

.overlay-container {
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-container.open {
  pointer-events: auto;
}

.overlay-content {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  overflow: auto;
  z-index: 9999999;
  overflow: hidden;
}

.overlay-close-btn {
  position: absolute;
  right: 10px;
  top: 10px;
}

.overlay-close-btn img {
  width: 20px;
  cursor: pointer;
}

